.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

b {
  padding-left: 10px;
  padding-top: 12px;
  font-size: medium;
}

a:hover  {
  background-color: yellow;
}

button.add-to-cart:hover {
  background-color: yellow;
}

a {
  font-weight: bold;
}

header {
  display: flex;
  justify-content: space-evenly;
}

.logo {
  flex: 30%;
  padding-left: 10%;
}

.navbar {
  flex: 70%;
  text-align: center;
}

.list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  margin-bottom: 20%;
}


button {
  background-color: white; 
  margin-left: 5px;
}

body {
  font-family: 'Inter', Arial, Helvetica, sans-serif;
}

.cinnamon-roll {
  display: flex;
  flex-direction: column;
}

.options-box {
  flex: 1;
  display: flex;
  padding-top: 10px;
  justify-content: space-evenly;
}

.options-label {
  justify-content: flex-start;
  width: auto;
  font-size: 12px;
  margin-bottom: auto;
  padding-left: 10%;
  padding-right: 10%;
}

.options {
  font-size: 12px;
  display: flex;
}

.cinnamon-roll {
  height: 180px;
  width: 180px;
}

.size { 
  height: 30px;
  width: 30px;
}

img{
  width: 250px;
  height: 250px;
}

.roll-picture {
  border: 3px solid black;
}
